@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;1,400&family=Playfair+Display:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.MuiTableCell-root {
  font-family: 'Montserrat', sans-serif !important;
}



.App {
  text-align: center;
}

.header-title {
  font-family: 'Playfair Display', serif;
  font-weight: 400;
  font-size: calc(16px + 5.5vmin);
  margin: auto;
  margin-top: 2%;
  padding: 15px;
  width: 80%;
}

.ivar-logo {
  width: calc(50px + 6vmin);
  display:inline-block;
  margin-bottom: 1%;
}

.ornament {
  width: calc(50px + 20vmin);
  display:inline-block;
}
  

.rule-header h2 {
    font-family: 'Playfair Display', serif;
    font-size: calc(14px + 0.5vmin);
}

.rule-box {
    font-family: 'Montserrat', sans-serif;
    text-align: left;
    padding: 0 25px 25px 25px;
    font-size: 16px;
}

.rule-box p {
    text-align: justify;
}

.rule-header {
    color: white;
    background-color: black;
    text-align: left;
    padding-left: 25px;
}

p.eksempel, span.eksempel {
    font-style: italic;
}

.emphasis {
    font-weight: 500;
}

table {
    table-layout: fixed;
    width: 100%;;
}

.hash-link {
    text-decoration: none;
}

.hash-link:focus, .hash-link:hover, .hash-link:visited, .hash-link:link, .hash-link:active {
    text-decoration: none;
    color:inherit
}


/**************
Book reference
***************/

p #title {
    font-style: italic;
    text-decoration: underline;
    color: lightseagreen
}

p #year {
    color: lightslategray;
}

.quote::before {
    content: open-quote;
}

.quote::after {
    content: close-quote;
}

.quote {
    quotes: "“" "”" "‘" "’";
}

.book-quote {
    border-left: 2px solid #949494;
    padding-left: 10px;
}


@media only screen and (max-width: 1000px) {
    table {
        word-break: break-word;
        overflow-wrap:break-word;
        -webkit-hyphens: auto;
            -ms-hyphens: auto;
                hyphens: auto;
    }
  }
